@-webkit-keyframes flex {
  0% {
    padding: 50px;
  }

  50% {
    padding: 20px;
  }

  100% {
    padding: 50px;
  }
}

@keyframes flex {
  0% {
    padding: 50px;
  }

  50% {
    padding: 20px;
  }

  100% {
    padding: 50px;
  }
}

.auth-lock svg {
